import React, { useCallback, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Button, FormControl, FormLabel, useStyleConfig, Text,
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import MiniBox from '../../../../layout/MiniBox';
import { useAppAsyncDispatch, useAppSelector } from '../../../../hooks/store';
import { createOwner, submitOwnerKyc, updateOwner } from '../../../../store/middlewares/business';
import { apiErrorHandler } from '../../../../utils/general';
import OwnershipPercentageInput from '../../../../components/OwnershipPercentageInput';

/**
 * Kyb Owners Ownership - Component for collecting the Ownership of the bussiness owners.
 * @module KYB
 * @returns {JSX.Element}
 */
const TheirOwnerShip = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const dispatch = useAppAsyncDispatch();

  const style:any = useStyleConfig('FormControl', {
    variant: 'primary'
  });

  const { selectedBusinessOwner, selectedBusiness } = useAppSelector((store) => store.business, shallowEqual);

  const [ownership, updateOwnerShip] = useState<number | ''>(selectedBusinessOwner?.ownership ? parseInt(selectedBusinessOwner?.ownership, 10) : '');

  const isValid = useMemo(() => (!Number.isNaN(ownership) && ownership >= 25 && ownership <= 100), [ownership]);

  const ownerKyc = useCallback((id: string) => {
    dispatch(submitOwnerKyc(id))
      .then(() => {
        navigate('/ao/kyb/ownersList');
      }).catch(apiErrorHandler);
  }, []);

  const submit = useCallback(() => {
    if (selectedBusinessOwner?.id && selectedBusinessOwner.person.address) {
      dispatch(updateOwner(selectedBusinessOwner?.id, {
        ownership: `${ownership}`,
        person: {
          firstName: selectedBusinessOwner.person.firstName,
          lastName: selectedBusinessOwner.person.lastName,
          email: selectedBusinessOwner.person.email,
          dateOfBirth: selectedBusinessOwner.person.dateOfBirth,
          phone: selectedBusinessOwner.person.phone,
          idNumber: selectedBusinessOwner.person.idNumber.replaceAll('-', ''),
          idType: selectedBusinessOwner.person.idType,
          address: selectedBusinessOwner.person.address
        }
      })).then(() => {
        setTimeout(() => {
          ownerKyc(selectedBusinessOwner.id);
        }, 0);
      }).catch(apiErrorHandler);
    } else if (selectedBusinessOwner && selectedBusiness && selectedBusinessOwner.person.address) {
      dispatch(createOwner({
        businessId: selectedBusiness.id,
        ownership: `${ownership}`,
        person: {
          firstName: selectedBusinessOwner.person.firstName,
          lastName: selectedBusinessOwner.person.lastName,
          email: selectedBusinessOwner.person.email,
          dateOfBirth: selectedBusinessOwner.person.dateOfBirth,
          phone: selectedBusinessOwner.person.phone,
          idNumber: selectedBusinessOwner.person.idNumber.replaceAll('-', ''),
          idType: selectedBusinessOwner.person.idType,
          address: selectedBusinessOwner.person.address
        }
      })).then(({ id }) => {
        setTimeout(() => {
          ownerKyc(id);
        }, 0);
      }).catch(apiErrorHandler);
    }
  }, [selectedBusinessOwner, ownership]);

  return (
    <div className="page">
      <form onSubmit={(e) => {
        e.preventDefault();
        submit();
      }}
      >
        <MiniBox
          varient="box"
          heading={t('their_ownership.title')}
          footer={(
            <Box>
              <Text variant="small" pb="16px">{t('their_ownership.toHelp')}</Text>
              <Button data-testid="submit-btn" width="100%" variant="primary" disabled={!isValid} type="submit">
                {t('their_ownership.ctaText')}
              </Button>
            </Box>
)}
        >
          <Box width="100%">

            <FormControl variant="primary" style={style}>
              <FormLabel variant="primary">
                {t('their_ownership.enterTheirOwnership')}
              </FormLabel>
              <OwnershipPercentageInput placeholder={t('their_ownership.percentageValue')} value={ownership} onChange={updateOwnerShip} />
            </FormControl>

          </Box>
        </MiniBox>
      </form>
    </div>
  );
};

export default TheirOwnerShip;
